import "./App.css";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import { Button, Nav } from "react-bootstrap";
import { useState, useEffect } from "react";
import Products from "./components/Products";
import { Route, Routes, useNavigate } from "react-router-dom";
import Detail from "./components/Detail";
import NotFound from "./components/NotFound";
import About from "./components/About";
import Location from "./components/Location";
import Member from "./components/Member";
import Title from "./components/Title";
import Title2 from "./components/Title2";
import ComVeggie from "./components/ComVeggie";
import veggieData from "./db/veggie";
import data from "./db/fruit";
import Cart from "./components/Cart";
import axios from "axios";
import Board from "./components/Board";
import Footer from './components/Footer';

function App() {
  const [input, setInput] = useState("");
  const [fruit, setFruit] = useState(data);
  const [veggie, setVeggie] = useState(veggieData);
  const [veggieCount, setVeggieCount] = useState(1);
  const [cart, setCart] = useState(() => {
    try {
      const saved = localStorage.getItem("cart");
      return saved ? JSON.parse(saved) : {};
    } catch (e) {
      console.error(" cart  :", e);
      return {};
    }
  });

  const navigate = useNavigate();

  // cart   localStorage 
  useEffect(() => {
    try {
      localStorage.setItem("cart", JSON.stringify(cart));
    } catch (e) {
      console.error(" cart  :", e);
    }
  }, [cart]);

  //  +3 
  const handleLoadMoreVeggie = async () => {
    try {
      if (veggieCount === 1) {
        const result = await axios.get(
          "https://raw.githubusercontent.com/hayohio-bit/react_20251120/main/shop/public/data/veggie2.json"
        );
        setVeggie([...veggie, ...result.data]);
        setVeggieCount(2);
      } else if (veggieCount === 2) {
        const result = await axios.get(
          "https://raw.githubusercontent.com/hayohio-bit/react_20251120/main/shop/public/data/veggie3.json"
        );
        setVeggie([...veggie, ...result.data]);
        setVeggieCount(3);
      } else if (veggieCount >= 3) {
        alert("  .");
      }
    } catch (err) {
      console.error("  :", err);
      alert("   .");
    }
  };

  //  
  const sortByName = () => {
    const sortedFruit = [...fruit].sort((a, b) =>
      a.title > b.title ? 1 : -1
    );
    setFruit(sortedFruit);
  };

  const sortByPriceLowToHigh = () => {
    const sortedFruit = [...fruit].sort((a, b) => a.price - b.price);
    setFruit(sortedFruit);
  };

  const sortByPriceHighToLow = () => {
    const sortedFruit = [...fruit].sort((a, b) => b.price - a.price);
    setFruit(sortedFruit);
  };

  return (
    <div className="App">
      {/*    */}
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand
            onClick={() => navigate("/")}
            style={{ cursor: "pointer" }}
          >
            
          </Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => navigate("/")}></Nav.Link>
            <Nav.Link onClick={() => navigate("/detail/fruit/1")}>
              
            </Nav.Link>
            <Nav.Link onClick={() => navigate("/cart")}></Nav.Link>
            <Nav.Link onClick={() => navigate("/about")}></Nav.Link>
            <Nav.Link onClick={() => { navigate("/board");}}></Nav.Link>

          </Nav>
        </Container>
      </Navbar>

      <Routes>
        {/* ()  */}
        <Route
          path="/"
          element={
            <div>
              {/*  */}
              <div
                className="slider"
                style={{ backgroundImage:
`url(${process.env.PUBLIC_URL + '/img/slider.jpg'})`, }}></div>

              <Title />

              {/*  +   */}
              <div className="container" style={{ marginTop: "30px" }}>
                <div className="row">
                  {/*   */}
                  <div className="col-md-6" style={{ textAlign: "left" }}>
                    <input
                      placeholder=" "
                      value={input}
                      onChange={(e) => setInput(e.target.value)}
                      style={{
                        padding: "10px",
                        marginLeft: "10px",
                        borderRadius: "4px",
                        border: "1px solid #ccc",
                        width: "250px",
                        marginRight: "10px",
                      }}
                    />
                  </div>

                  {/*  (select) */}
                  <div className="col-md-6" style={{ textAlign: "right" }}>
                    <select
                      onChange={(e) => {
                        if (e.target.value === "low") sortByPriceLowToHigh();
                        if (e.target.value === "high") sortByPriceHighToLow();
                        if (e.target.value === "name") sortByName();
                      }}
                      style={{
                        padding: "10px",
                        marginLeft: "10px",
                        borderRadius: "4px",
                        border: "1px solid #ccc",
                        width: "150px",
                      }}
                    >
                      <option value=""> </option>
                      <option value="low"> </option>
                      <option value="high"> </option>
                      <option value="name"></option>
                    </select>
                  </div>
                </div>
              </div>

              {/*   ( +  ) */}
              <div className="container" style={{ marginTop: "30px" }}>
                <div className="row">
                  {fruit
                    .filter((item) =>
                      item.title
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    )
                    .map((item) => (
                      <Products {...item} key={item.id} />
                    ))}
                </div>
              </div>

              {/*   */}
              <div className="container" style={{ marginTop: "70px" }}>
                <Title2 />
                <div className="row" style={{ marginTop: "20px" }}>
                  {veggie.map((item) => (
                    <ComVeggie {...item} key={item.id} />
                  ))}
                </div>

                <div style={{ textAlign: "center", marginTop: "5px", marginBottom:"50px" }}>
                  <Button
                    variant="outline-success"
                    onClick={handleLoadMoreVeggie}
                  >
                    + 3   
                  </Button>
                </div>
              </div>
            </div>
          }
        />

        {/*   (fruit / veggie ) */}
        <Route
          path="/detail/:type/:id"
          element={
            <Detail
              fruit={fruit}
              veggie={veggie}
              onAddToCart={(item, type) => {
                const key = `${type}-${item.id}-${item.title}`;
                  setCart((prevCart) => ({
                    ...prevCart,
                    [key]: prevCart[key]
                      ? {
                          ...prevCart[key],
                          quantity: prevCart[key].quantity + 1,
                        }
                      : { ...item, type, quantity: 1 },
                }));
              }}
            />
          }
        />

        {/*  */}
          <Route
          path="/cart"
          element={
            <Cart
              cart={cart}
              onRemoveItem={(id, title, type) => {
                const key = `${type}-${id}-${title}`;
                setCart((prevCart) => {
                  const newCart = { ...prevCart };
                  delete newCart[key];
                  return newCart;
                });
              }}
              onUpdateQuantity={(id, title, type, quantity) => {
                const key = `${type}-${id}-${title}`;
                if (quantity <= 0) {
                  setCart((prevCart) => {
                    const newCart = { ...prevCart };
                    delete newCart[key];
                    return newCart;
                  });
                } else {
                  setCart((prevCart) => ({
                    ...prevCart,
                    [key]: { ...prevCart[key], quantity },
                  }));
                }
              }}
            />
          }
        />

        {/*  +   */}
        <Route path="/about" element={<About />}>
          <Route path="member" element={<Member />} />
          <Route path="location" element={<Location />} />
        </Route>

        {/*  */}
        <Route path="/board" element={<Board />} />

        {/* 404  */}
        <Route path="*" element={<NotFound />} />
      </Routes>
      <Footer />
    </div>
  );
}

export default App;
