import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import { Button, Nav } from 'react-bootstrap';

function Detail({ fruit, veggie, onAddToCart }) {
    const { type, id } = useParams();         // URL /detail/:id  id 
    const numericId = Number(id);       // URL      
    const [tap, setTap] = useState(0);
    
    // 1) type     
    const sourceArray =
        type === "fruit" ? fruit :
        type === "veggie" ? veggie :
        null;

    // 2) type ,   
    if (!sourceArray) {
        return <div className="container mt-5"> .</div>;
    }

    // 3) id    
    const item = sourceArray.find((f) => f.id === numericId);

    // 4)  id    
    if (!item) {
        return <div className="container mt-5">  .</div>;
    }

const { id: itemId, imgUrl, title, content, price, weight, rating, reviews } = item;

    return (
        <div className="container">
            <div className="row">
                {/*   */}
                <div className="col-md-6">
                <img
                src={process.env.PUBLIC_URL+"/"+imgUrl}
                style={{ width: "100%", maxWidth: "400px", height: "auto" }}
                alt={title}
                />
                </div>

                {/*   */}
                <div className="col-md-6">
                <h5 className="pt-5">{title}</h5>
                <p>{content}</p>
                {weight && <p>{weight}</p>}

                {rating && (
                    <p style={{ margin: "5px 0" }}>
                    {"".repeat(rating)}
                    {"".repeat(5 - rating)} ({rating}.0)   {reviews}
                    </p>
                )}

                <p>{price.toLocaleString()}</p>

                <button
                className="btn btn-primary"
                onClick={() => onAddToCart(item, type)}
                >
                 
                </button>

                <Link to="/cart">
                    <Button variant="outline-success"> </Button>
                </Link>
                </div>
            </div>

                <Nav
                variant="tabs"
                defaultActiveKey="link0"
                style={{ marginTop: "50px" }}
                >
                <Nav.Item>
                <Nav.Link onClick={() => setTap(0)} eventKey="link0">
                    0
                </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                <Nav.Link onClick={() => setTap(1)} eventKey="link1">
                    1
                </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                <Nav.Link onClick={() => setTap(2)} eventKey="link2">
                    2
                </Nav.Link>
                </Nav.Item>
                </Nav>

                <TabContent tap={tap} />
                </div>
    );
}

                function TabContent({ tap }) {
                    const [fade, setFade] = useState("");

                    useEffect(() => {
                        const timer = setTimeout(() => setFade("end"), 500);
                        return () => {
                        clearTimeout(timer);
                        setFade("");
                        };
                    }, [tap]);

                    return (
                        <div className={"start " + fade}>
                        {[<div>0</div>, <div>1</div>, <div>2</div>][tap]}
                        </div>
                    );
                    }

export default Detail;