    import React, { useState } from "react";
    import { Button, Table, Form, Alert } from "react-bootstrap";

    const FruitBoard = () => {
    //    
    const [boardList, setBoardList] = useState([
        {
        no: "1",
        title: "   ?",
        description: "    .",
        viewCount: 1,
        },
        {
        no: "2",
        title: " ",
        description:
            "  .    . ",
        viewCount: 2,
        },
        {
        no: "3",
        title: "  ",
        description: " 11birx .",
        viewCount: 1,
        },
        {
        no: "4",
        title: " ",
        description: "  .",
        viewCount: 1,
        },
    ]);

    // UI 
    const [listOk, setListOk] = useState(true); //  
    const [readOk, setReadOk] = useState(false); //  
    const [writeOk, setWriteOk] = useState(false); //  
    const [editOk, setEditOk] = useState(false); //  
    const [boardInfo, setBoardInfo] = useState({}); //     

    //   
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");

    //   
    const [editNo, setEditNo] = useState(null); //   
    const [editTitle, setEditTitle] = useState("");
    const [editDescription, setEditDescription] = useState("");

    //   
    const [errorMessage, setErrorMessage] = useState("");

    //   
    const boardListView = () => {
        setReadOk(false);
        setWriteOk(false);
        setEditOk(false);
        setListOk(true);
    };

    //  
    const boardRead = (no) => {
        setListOk(false);
        setWriteOk(false);
        setEditOk(false);
        setReadOk(true);

        //  
        const updatedList = boardList.map((b) =>
        b.no === no ? { ...b, viewCount: b.viewCount + 1 } : b
        );
        setBoardList(updatedList);

        const selectedBoard = boardList.find((b) => b.no === no);
        setBoardInfo(selectedBoard);
    };

    //    
    const boardWrite = () => {
        setListOk(false);
        setWriteOk(true);
    };

    //   
    const boardSave = () => {
        if (title.trim() === "" || description.trim() === "") {
        setErrorMessage("   !");
        return;
        }

        const newBoard = {
        no: (boardList.length + 1).toString(),
        title,
        description,
        viewCount: 0,
        };
        setBoardList([...boardList, newBoard]);
        setTitle("");
        setDescription("");
        setErrorMessage("");
        boardListView();
    };

    //  
    const boardDelete = (no) => {
        const updatedList = boardList.filter((b) => b.no !== no.toString());
        setBoardList(updatedList);
        boardListView();
    };

    //    
    const boardEdit = (no) => {
        setEditOk(true);
        setListOk(false);

        const boardToEdit = boardList.find((b) => b.no === no);
        setEditNo(boardToEdit.no);
        setEditTitle(boardToEdit.title);
        setEditDescription(boardToEdit.description);
    };

    //   
    const updateBoard = () => {
        const updatedBoardList = boardList.map((b) =>
        b.no === editNo
            ? { ...b, title: editTitle, description: editDescription }
            : b
        );
        setBoardList(updatedBoardList);
        boardListView();
    };

    return (
        <div className="container board-wrap">
        <div className="board-header">
            <h3 className="board-title"> </h3>
            <span className="board-subtitle">
            , ,      .
            </span>
        </div>   

        {/*   */}
        {listOk && (
        <>
            <div className="board-list">
            {boardList
                .slice()
                .reverse()
                .map((board) => (
                <div key={board.no} className="board-item">
                    <div
                    className="board-item-title"
                    onClick={() => boardRead(board.no)}
                    >
                    {board.title}
                    </div>
                    <div
                    className="board-item-desc"
                    onClick={() => boardRead(board.no)}
                    >
                    {board.description}
                    </div>
                    <div className="board-item-meta">
                    <span>No. {board.no}   {board.viewCount}</span>
                    <div className="board-item-buttons">
                        <Button
                        variant="outline-primary"
                        size="sm"
                        onClick={() => boardRead(board.no)}
                        >
                        
                        </Button>
                        <Button
                        variant="outline-success"
                        size="sm"
                        onClick={() => boardEdit(board.no)}
                        >
                        
                        </Button>
                        <Button
                        variant="outline-danger"
                        size="sm"
                        onClick={() => boardDelete(board.no)}
                        >
                        
                        </Button>
                    </div>
                    </div>
                </div>
                ))}
            </div>

            <div className="board-list-actions">
            <Button variant="primary" onClick={boardWrite}>
                 
            </Button>
            </div>
        </>
        )}

        {/*   */}
        {readOk && (
        <div>
            <h5 className="board-read-title">{boardInfo.title}</h5>
            <hr />
            <p className="board-read-body">{boardInfo.description}</p>
            <div className="board-form-actions">
            <Button variant="secondary" onClick={boardListView}>
                
            </Button>
            </div>
        </div>
        )}
        {/*     */}
        {writeOk && (
            <div>
            <h5 className="board-form-title">  </h5>

            {errorMessage && <Alert variant="danger">{errorMessage}</Alert>}

            <Form.Group controlId="formName">
                <Form.Control
                type="text"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                placeholder="  "
                />
            </Form.Group>
            <Form.Group
                controlId="formDescription"
                style={{ marginTop: "20px" }}
            >
                <Form.Control
                as="textarea"
                rows={3}
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                placeholder="  "
                />
            </Form.Group>

            <div className="board-form-actions">
                <Button
                variant="primary"
                onClick={boardSave}
                style={{ marginRight: "10px" }}
                >
                
                </Button>
                <Button variant="secondary" onClick={boardListView}>
                
                </Button>
            </div>
            </div>
        )}

        {/*   */}
        {editOk && (
            <div>
            <h5 className="board-edit-title"> </h5>

            <Form.Group controlId="formEditName">
                <Form.Control
                type="text"
                value={editTitle}
                onChange={(e) => setEditTitle(e.target.value)}
                placeholder=" "
                />
            </Form.Group>
            <Form.Group
                controlId="formEditDescription"
                style={{ marginTop: "20px" }}
            >
                <Form.Control
                as="textarea"
                rows={3}
                value={editDescription}
                onChange={(e) => setEditDescription(e.target.value)}
                placeholder=" "
                />
            </Form.Group>

            <div className="board-form-actions">
                <Button
                variant="outline-success"
                onClick={updateBoard}
                style={{ marginRight: "10px" }}
                >
                
                </Button>
                <Button variant="outline-info" onClick={boardListView}>
                
                </Button>
            </div>
            </div>
        )}
        </div>
    );
    };

    export default FruitBoard;
