import React from 'react';

    const Title2 = () => {
    const wrapperStyle = {
        marginTop: '70px',
        textAlign: 'center',
    };

    const subTextStyle = {
        marginTop: '8px',
        color: '#555',
        fontSize: '0.95rem',
    };

    return (
        <div style={wrapperStyle}>
        <h3>. TOP</h3>
        <p style={subTextStyle}>   .</p>
        </div>
    );
    };

export default Title2;